html {
  box-sizing: border-box;
  -ms-overflow-style: scrollbar; }

*,
*::before,
*::after {
  box-sizing: inherit; }

body {
  margin: 0;
  padding: 0;
  font-family: "Open Sans", sans-serif;
  color: #363946; }

html,
body {
  font-size: 16px;
  line-height: 1.2; }

.clearfix:after {
  content: "";
  display: table;
  clear: both; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden; }

.wrapper {
  display: flex;
  min-height: 100vh;
  flex-direction: column;
  overflow: hidden; }

.content {
  flex-grow: 1;
  width: 100%;
  position: relative; }

::-webkit-input-placeholder {
  color: #363946; }

::-moz-placeholder {
  color: #363946; }

:-moz-placeholder {
  color: #363946; }

:-ms-input-placeholder {
  color: #363946; }
